import React from 'react';
const PUBLIC_KEY = '56889379e12b179fea9f6dc3d84f38d73a11ae82478f8355e1950a9045575780'

class PhotoPage extends React.Component {
  state = {image:null};
  componentDidMount() {
    fetch('https://api.unsplash.com/photos/random?&client_id='+PUBLIC_KEY,{method:'GET'})
    .then(res=>res.json())
    .then(res=>{
      this.setState({image: res})
      })
  }

  render(){
    let obj = this.state.image;
    console.log(obj)
    if(this.state.image!=null){
      return (
        <div className = 'photoPage' >
          <div className='photoPage__group-left'>
            <img src={this.state.image.urls.small}></img>
          </div>
          <div className='photoPage__group-right'>
            <h1>{`${obj.user.first_name} ${obj.user.last_name}`}</h1>
            <p>{obj.description}</p>
          </div>
        </div>
      )
    }else{
      return (
        <div className = 'photoPage'>
        nothing photo
        </div>
      )
    }
  }
}

export default PhotoPage;